import React, {useState} from 'react';
import JobsList from "./view-jobs";
import Header from "./header";
import './App.css';

function App() {

  const [jobs, setJobs] = useState([]);

  const cors_anywhere = "https://cors-anywhere.herokuapp.com";
  const api_origin = "https://jobs.github.com/positions.json";
  
  const getjobs = async (arg) => {
    setJobs([]);
    document.querySelector('#loading_start').style.display = "block";
    const api_url = `${cors_anywhere}/${api_origin}?description=${arg.name}&location=${arg.location}`;
    console.log("fetch api starts");
    const response = await fetch(api_url);
    const api_data = await response.json();
    console.log("fetch api ends");
    setJobs(api_data);
    document.querySelector('#loading_start').style.display = "none";
    document.querySelector('#jobs_found').style.display = "block";
  }

  const update_search = e => {
    document.querySelector('#jobs_found').style.display = "none";
    e.preventDefault();
    var input_job = document.querySelector('#search_input').value;
    var input_location = document.querySelector('#search_location').value;
    console.log(input_job + ' in ' + input_location);
    getjobs({name: input_job, location: input_location});
  }

  return (
    <div className="App">
      <Header />
      <div className="container">
      <form className="search-form form-inline" onSubmit={update_search}>
        <input id="search_input" required placeholder="job title" type="text" className="form-control"/>
        <input id="search_location" placeholder="job location" type="text" className="form-control"/>
        <button type="submit" className="btn btn-primary">submit</button>
        <div id="loading_start">loading..</div>
      </form>
      </div>
      <div className="container">
        <div className="jobs-found" id="jobs_found">
        <h1>No of jobs Found : {jobs.length}</h1>
        </div>
      <div className="jobs-list row">
      {
        jobs.map(job =>(
        <div key={job.id} className="col-lg-6 col-md-6 column">
          <JobsList job={job} />
        </div>
      ))}
      </div>
      </div>
    </div>
  );
}

export default App;
